<template>
  <CContainer class="d-flex content-center min-vh-100">
      <CCol md="6">
          <CCard class="p-4">
            <CCardBody>
              <CForm @submit.prevent="handleSubmit()">
                <h1>Login</h1>
                <p class="text-muted">Sign In to your account</p>
                <CInput
                  placeholder="Email"
                  autocomplete="username email"
                  v-model="email"
                >
                  <template #prepend-content><CIcon name="cil-user"/></template>
                </CInput>
                <CInput
                  placeholder="Password"
                  type="password"
                  autocomplete="curent-password"
                  v-model="password"
                >
                  <template #prepend-content><CIcon name="cil-lock-locked"/></template>
                </CInput>
                <CRow>
                  <CCol col="6" class="text-left">
                    <CButton color="primary" type="submit" class="px-4">Login</CButton>
                  </CCol>
                  <CCol col="6" class="text-right">
                    <CButton color="link" class="px-0">Forgot password?</CButton>
                    <CButton color="link" class="d-md-none">Register now!</CButton>
                  </CCol>
                </CRow>
              </CForm>
            </CCardBody>
          </CCard>
      </CCol>
  </CContainer>
</template>

<script>
export default {
  name: 'Login',
  data: function() {
    return {
      email: "",
      password: "",
      errors: ""
    };
  },
  methods: {
    async handleSubmit() {
      let email = this.email;
      let password = this.password;
      this.$store
        .dispatch("login", { email, password })
        .then(user => {
          sessionStorage.setItem("user", user._id);
          sessionStorage.setItem("username", user.firstname + " " + user.lastname);
        })
        .catch(err => {
          if(err.err.response.status == 404) {
            this.$swal.fire("User Not Found","Check the email again.","error");
          }else if(err.err.response.status == 401) {
            this.$swal.fire("Password is Wrong","Check the password again.","error");
          }
        });
    },
  }
}
</script>
